module.exports = toCollapseMap;

function toCollapseMap(paths, collapseMap) {
    return paths.reduce(function(acc, path) {
        var len = path.length;
        if (!acc[len]) {
            acc[len] = [];
        }
        acc[len].push(path);
        return acc;
    }, collapseMap || {});
}



